.footer {
    padding: 30px 4%;
    max-width: 1000px;
    margin: 0 auto;
}

.footer-icons {
    display: flex;
    gap: 20px;
    margin: 40px 0;
}

.footer-icons img {
    width: 30px;
    cursor: pointer;
}

.footer ul{
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 16px;
    margin-bottom: 32px;
    list-style: none;
}

.copyright-text {
    color: grey;
    font-size: 14px;
}

@media (max-width: 800px) {
    .footer-icons img{
        width: 24px;
    }
    .footer ul{
        grid-template-columns: auto auto;
        gap: 8px;
        font-size: 14px;
    }
}